import React from 'react';
import PropTypes from 'prop-types';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import styles from './product-information.module.scss';

const ProductInformation = ({ data }) => {
  const { ingredientTab, nutritionTab } = data;

  return (
    <Tabs>
      <TabList className={styles.tab_list}>
        <Tab className={styles.tab} selectedClassName={styles.tab__selected}>
          {ingredientTab.label}
        </Tab>
        <Tab className={styles.tab} selectedClassName={styles.tab__selected}>
          {nutritionTab.label}
        </Tab>
      </TabList>

      <TabPanel className={styles.tab_panel}>
        <p>{ingredientTab.ingredients}</p>
      </TabPanel>
      <TabPanel className={styles.tab_panel}>
        <table className={styles.table}>
          <tbody>
            {nutritionTab.table.map(item => {
              return (
                <tr>
                  {item.isTableHeader
                    ? item.cellText.map(text => <th>{text}</th>)
                    : item.cellText.map(text => <td>{text}</td>)}
                </tr>
              );
            })}
          </tbody>
        </table>
      </TabPanel>
    </Tabs>
  );
};

ProductInformation.propTypes = {
  data: PropTypes.shape({
    ingredientTab: PropTypes.shape({
      label: PropTypes.string,
      ingredients: PropTypes.string,
    }),
    nutritionTab: PropTypes.shape({
      label: PropTypes.string,
      table: PropTypes.arrayOf(
        PropTypes.shape({
          isTableHeader: PropTypes.bool,
          cellText: PropTypes.arrayOf(PropTypes.string),
        })
      ),
    }),
  }).isRequired,
};

export default ProductInformation;
