import React from 'react';
import PropTypes from 'prop-types';
import Carousel from '@components/carousel/carousel';
import { Table, Thead, Tbody, Tr, Th, Td } from 'react-super-responsive-table';
import 'react-super-responsive-table/dist/SuperResponsiveTableStyle.css';
import styles from './product-description.module.scss';

const ProductDescription = ({ data }) => {
  const { name, description, orderSize, benefits, images } = data;

  const orderSizeHeaders =
    orderSize && orderSize.filter(item => item.isTableHeader === true)[0];
  const orderSizeData =
    orderSize && orderSize.filter(item => item.isTableHeader === false);

  return (
    <div className={styles.product_description}>
      <div className={styles.product_description_carousel}>
        <Carousel images={images} />
      </div>
      <div>
        <h2>{name}</h2>
        {description && <p>{description}</p>}
        {orderSize && (
          <Table className={styles.product_description_table}>
            <Thead>
              <Tr>
                {orderSizeHeaders.cellText.map((text, index) => (
                  <Th key={index}>{text}</Th>
                ))}
              </Tr>
            </Thead>
            <Tbody>
              {orderSizeData.map((item, index) => (
                <Tr key={index}>
                  {item.cellText.map((text, index) => (
                    <Td key={index}>{text}</Td>
                  ))}
                </Tr>
              ))}
            </Tbody>
          </Table>
        )}
        <h3 className={styles.product_description_benefit_title}>Benefits:</h3>
        <ul className={styles.product_description_benefits}>
          {benefits.map((benefit, index) => (
            <li key={index}>{benefit}</li>
          ))}
        </ul>
      </div>
    </div>
  );
};

ProductDescription.propTypes = {
  data: PropTypes.shape({
    name: PropTypes.string,
    description: PropTypes.string,
    orderSize: PropTypes.arrayOf(
      PropTypes.shape({
        isTableHeader: PropTypes.bool,
        cellText: PropTypes.arrayOf(PropTypes.string),
      })
    ),
    benefits: PropTypes.arrayOf(PropTypes.string),
    images: PropTypes.arrayOf(PropTypes.string),
  }).isRequired,
};

export default ProductDescription;
