import React from 'react';
import { graphql } from 'gatsby';
import Layout from '@components/layout/layout';
import Container from '@components/layout/container/container';
import Seo from '@components/seo';
import ProductDescription from '@templates/product-details/product-description/product-description';
import ProductInformation from '@templates/product-details/product-information/product-information';

const ProductDetails = ({ data }) => {
  const { header, content, footer } = data.jsonJson;
  const { productDescription, productInformation } = content;

  return (
    <>
      <Seo title={productDescription.name} />
      <Layout header={header} footer={footer}>
        <Container>
          <ProductDescription data={productDescription} />
          {productInformation && (
            <ProductInformation data={productInformation} />
          )}
        </Container>
      </Layout>
    </>
  );
};

export const query = graphql`
  query pageData($id: String) {
    jsonJson(id: { eq: $id }) {
      header {
        navLinks {
          isMobileOnly
          label
          url
        }
      }
      footer {
        columnLinks1 {
          label
          url
        }
        columnLinks2 {
          label
          url
        }
      }
      content {
        productDescription {
          benefits
          description
          name
          orderSize {
            cellText
            isTableHeader
          }
          images
        }
        productInformation {
          ingredientTab {
            ingredients
            label
          }
          nutritionTab {
            label
            table {
              isTableHeader
              cellText
            }
          }
        }
      }
    }
  }
`;

export default ProductDetails;
