import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { useStaticQuery, graphql } from 'gatsby';
import classNames from 'classnames/bind';
import Button, { BtnColor } from '@components/button/button';

import styles from './header.module.scss';

const cx = classNames.bind(styles);

const Header = ({ data }) => {
  const queryResult = useStaticQuery(graphql`
    query {
      allFile(filter: { extension: { eq: "png" } }) {
        edges {
          node {
            publicURL
            name
          }
        }
      }
    }
  `);

  const [isMenuOpen, setIsMenuOpen] = useState(true);
  const [isProductMenuOpen, setIsProductMenuOpen] = useState(false);

  const { navLinks } = data;

  const getImage = imageName =>
    queryResult.allFile.edges.find(file => file.node.name === imageName);

  return (
    <header className={styles.header}>
      <button
        className={styles.hamburger}
        onClick={() => setIsMenuOpen(!isMenuOpen)}
      >
        <div className={styles.hamburger__bar} />
        <div className={styles.hamburger__bar} />
        <div className={styles.hamburger__bar} />
      </button>
      <div className={styles.header__main}>
        <div className={styles.brandLogo}>
          <a href="../index.html">
            <img src={getImage('sisco-seafoods-200x92').node.publicURL} />
          </a>
        </div>
        {isMenuOpen && (
          <ul className={styles.nav__list}>
            <li
              className={styles.desktopOnly}
              onMouseOver={() => setIsProductMenuOpen(!isProductMenuOpen)}
            >
              <a>PRODUCTS</a>
            </li>
            {navLinks.map((nav, index) => (
              <li key={index} className={cx({ mobileOnly: nav.isMobileOnly })}>
                <a href={nav.url}>{nav.label}</a>
              </li>
            ))}
          </ul>
        )}
      </div>
      <div className={styles.orderbar}>
        <a href="https://www.siscofoods.com/files/Product_Catalogue_2019_SEAFOOD.pdf">
          DOWNLOAD OUR 2019 SEAFOODS CATALOGUE
        </a>
      </div>
      {isProductMenuOpen && (
        <div className={styles.product__dropdown}>
          <div className={styles.product__dropwdown_item1}>
            <img src={getImage('nav-drinks').node.publicURL} />
            <div className={styles.product__dropdown_text}>
              <p>COMING SOON</p>
              <p>Get your taste buds tingling</p>
            </div>
            <a href={navLinks[0].url}>
              <Button color={BtnColor.RED}>Coming soon</Button>
            </a>
          </div>
          <div>
            <img src={getImage('nav-foods').node.publicURL} />
            <div className={styles.product__dropdown_text}>
              <p>COMING SOON</p>
              <p>Delicious foods from around the world</p>
            </div>
            <a href={navLinks[1].url}>
              <Button color={BtnColor.GREEN}>Coming soon</Button>
            </a>
          </div>
          <div className={styles.product__dropwdown_item3}>
            <img src={getImage('nav-seafoods').node.publicURL} />
            <div className={styles.product__dropdown_text}>
              <p>SISCO SEAFOODS</p>
              <p>Take a dive into our extensive range</p>
            </div>
            <a href={navLinks[2].url}>
              <Button color={BtnColor.BLUE}>Discover more</Button>
            </a>
          </div>
        </div>
      )}
    </header>
  );
};

Header.propTypes = {
  data: PropTypes.shape({
    navLinks: PropTypes.arrayOf(
      PropTypes.shape({ label: PropTypes.string, url: PropTypes.string })
    ),
  }).isRequired,
};

export default Header;
